import { State } from "./State";
import { Selection } from "./Selection";
const MAX_STATE_STACK = 10;
export class Project {
    constructor() {
        this._document = null;
        // private _masterDocument: Document;
        this._documentList = new Set();
        this._state = new Map();
        this._middleware = null;
        this._selection = new Selection();
    }
    dispose() {
        if (this._documentList == null) {
            // Already disposed
            return;
        }
        const state = this._state;
        this._state = null;
        // Clear document list
        if (this._documentList) {
            for (const document of this._documentList) {
                if (state.has(document.id)) {
                    state.get(document.id).dispose();
                    state.delete(document.id);
                }
                document.dispose();
            }
            this._documentList.clear();
            this._documentList = null;
        }
        this._document = null;
        // this._masterDocument = null;
        // Clear left-over state
        for (const st of state.values()) {
            st.dispose();
        }
        state.clear();
        // Clear selection
        this._selection.dispose();
        this._selection = null;
        // Remove middleware
        this._middleware = null;
    }
    hasDocument(document) {
        return this._documentList.has(document);
    }
    addDocument(document) {
        if (document.project != null) {
            // Document must not be attached to other project
            return false;
        }
        if (this._documentList.has(document)) {
            return false;
        }
        for (const doc of this._documentList) {
            if (doc.id === document.id) {
                return false;
            }
        }
        // Link to current project
        document.project = this;
        this._documentList.add(document);
        if (this._document == null) {
            this.setCurrentDocument(document);
        }
        // if (this._masterDocument == null) {
        //     this._masterDocument = document;
        // }
        return true;
    }
    /**
     * Removes the document and clears document state
     * If the current document is the document you want to remove, the document will not be removed
     * Optionally you can dispose the document
     * You cannot remove the master document
     */
    removeDocument(document, dispose = false) {
        if (!this._documentList.has(document) || this._document === document /*this._masterDocument === document*/) {
            return false;
        }
        this._documentList.delete(document);
        if (this._state.has(document.id)) {
            this._state.get(document.id).dispose();
            this._state.delete(document.id);
        }
        // if (this._document === document) {
        //     this.setCurrentDocument(this._masterDocument);
        // }
        if (dispose) {
            document.dispose();
        }
        // Unlink from current project
        document.project = null;
        return true;
    }
    /**
     * Get an iterator to documents
     */
    getDocuments() {
        return this._documentList.values();
    }
    get selection() {
        return this._selection;
    }
    get middleware() {
        if (this._middleware == null) {
            this._middleware = this.createMiddleware();
        }
        return this._middleware;
    }
    // get masterDocument(): Document {
    //     return this._masterDocument;
    // }
    get document() {
        return this._document;
    }
    set document(value) {
        if (this._documentList.has(value)) {
            this.setCurrentDocument(value);
        }
    }
    setCurrentDocument(value, state) {
        this._document = value;
        this._selection.setDocument(value, state?.selection);
        if (!this._state.has(value.id)) {
            this._state.set(value.id, this.createState(MAX_STATE_STACK));
        }
    }
    replaceDocument(next, meta) {
        // No longer needed, id is used as key
        // const state = this._state.get(current.document);
        // this._state.delete(current.document);
        // this._state.set(next.document, state);
        // if (this._masterDocument === current.document) {
        //     this._masterDocument = next.document;
        // }
        this.setCurrentDocument(next, meta);
        this._documentList.delete(this._document);
        this._documentList.add(next);
        return true;
    }
    createState(maxStack) {
        return new State(this, maxStack);
    }
    get state() {
        // if (!this._state.has(this._document.id)) {
        //     this._state.set(this._document.id, this.createState(MAX_STATE_STACK));
        // }
        return this._state.get(this._document.id);
    }
}
