import { BlendMode, DefaultPen, FillRule, PaintOrder, SolidBrush, Color } from "../Graphics";
export default class GlobalElementProperties {
    constructor() {
        // Vector element
        this.stroke = null;
        this.fill = null;
        this.fillRule = FillRule.NonZero;
        this.paintOrder = PaintOrder.FillStrokeMarkers;
        this.blendMode = BlendMode.Normal;
        this.opacity = 1;
        this.isolate = false;
        // Rect element
        this.rectRadius = null;
        // Regular polygon
        this.regularPolygonSides = 5;
        this.regularPolygonCornerRadius = 0;
        // Star
        this.starSides = 5;
        this.starInnerRadiusPercent = 0.4;
        this.starInnerCornerRadius = 0;
        this.starOuterCornerRadius = 0;
        this.starOuterRotate = 0;
        this.starInnerRotate = 0;
        this.stroke = new DefaultPen();
        //this.fill = SolidBrush.BLACK;
        this.fill = new SolidBrush(Color.from('violet'));
    }
    dispose() {
    }
}
