import { SolidBrush } from "../../Graphics";
import { Document } from "../Document";
const WHITE_BRUSH = SolidBrush.WHITE;
export default class BoardDocument extends Document {
    constructor(board, id) {
        super(id);
        this._board = board;
    }
    get size() {
        return this._board;
    }
    get board() {
        return this._board;
    }
    set board(value) {
        this._board = value;
        this.invalidateBoundsAndPicture(true);
    }
    get globalBounds() {
        if (this._globalBounds !== null) {
            return this._globalBounds;
        }
        return this._globalBounds = super.globalBounds.ensureMinSize(this._board);
    }
    get globalTightBounds() {
        if (this._globalTightBounds !== null) {
            return this._globalTightBounds;
        }
        return this._globalTightBounds = super.globalTightBounds.ensureMinSize(this._board);
    }
    render(engine) {
        const { context, theme, viewBox } = engine;
        context.clear(theme.background);
        context.save();
        context.multiplyMatrix(viewBox.matrix);
        // TODO: use custom brush
        context.drawRect(this._board, WHITE_BRUSH);
        const { grid, guides } = this;
        if (grid.drawToBack && grid.isVisible) {
            grid.render(engine);
        }
        this.draw(context);
        if (!grid.drawToBack && grid.isVisible) {
            grid.render(engine);
        }
        if (guides.isVisible) {
            guides.render(engine);
        }
        context.restore();
    }
    cloneCurrent(newId) {
        // @ts-ignore
        return new this.constructor(this._board, newId ? null : this._id);
    }
}
