export function resizeCanvasElement(canvas, width, height, dpr = 1) {
    canvas.style.width = width + 'px';
    canvas.style.height = height + 'px';
    canvas.width = width * dpr;
    canvas.height = height * dpr;
}
export class DPRObserver {
    constructor(callback, initial) {
        this.callback = callback;
        this.value = initial || window.devicePixelRatio;
        this.update = this.onChange.bind(this);
        this.updateListener();
    }
    onChange() {
        const dpr = window.devicePixelRatio;
        if (this.value !== dpr) {
            this.value = dpr;
            this.callback(dpr);
            this.updateListener();
        }
    }
    updateListener() {
        this.removeListener();
        this.media = matchMedia(`not screen and (resolution: ${this.value}dppx)`);
        this.media.addEventListener('change', this.update);
    }
    removeListener() {
        if (this.media) {
            this.media.removeEventListener('change', this.update);
            this.media = null;
        }
    }
    dispose() {
        this.removeListener();
        this.update = null;
        this.callback = null;
    }
}
