export class Keyframe {
    constructor(value, offset = 0, easing = null) {
        this.value = value;
        this.offset = offset;
        this.easing = easing;
    }
    clone() {
        // T should be immutable
        return new Keyframe(this.value, this.offset, this.easing);
    }
}
