import SkiaWasmInit from "@zindex/skia-ts";
import { CanvasEngine } from "./Engine";
import { FontManager } from "./Graphics";
export * from "./Animation";
export * from "./Model";
export * from "./Graphics";
export * from "./Project";
export * from "./Tools";
export * from "./Utils";
export * from "./Engine";
export async function CanvasEngineInit(init) {
    if (!window['Skia']) {
        // @ts-ignore
        window['Skia'] = await (init || SkiaWasmInit)(); // Define Skia as global
    }
    // Register default typeface
    FontManager.registerTypeface(Skia.SkTypeface.MakeFromData(await (await fetch('./font.ttf.woff2')).arrayBuffer()), true);
    customElements.define('canvas-engine', CanvasEngine);
    await customElements.whenDefined('canvas-engine');
}
