import { VectorElement } from "./VectorElement";
import { DefaultPen, EmptyBrush, FontManager, PaintOrder, Rectangle } from "../../Graphics";
export class TextElement extends VectorElement {
    constructor(document, text, font, id) {
        super(document, id);
        this._path = null;
        this._glyphs = null;
        this._text = text;
        this._font = font;
    }
    get stroke() {
        if (!this._stroke) {
            this._stroke = new DefaultPen(new EmptyBrush());
        }
        return this._stroke;
    }
    get type() {
        return "text";
    }
    /**
     * @inheritDoc
     */
    get localBounds() {
        if (this._localBounds === null) {
            this._localBounds = this.font.getGlyphsBounds(this.glyphs);
        }
        return this._localBounds;
    }
    /**
     * @inheritDoc
     */
    get localTightBounds() {
        return this.localBounds;
    }
    /**
     * @inheritDoc
     */
    get globalBounds() {
        if (this._globalBounds === null) {
            this._globalBounds = this.localBounds.transform(this.globalMatrix);
        }
        return this._globalBounds;
    }
    /**
     * @inheritDoc
     */
    get globalTightBounds() {
        return this.globalBounds;
    }
    get text() {
        return this._text;
    }
    set text(value) {
        if (this._text !== value) {
            this._text = value;
            this.invalidateText();
        }
    }
    get glyphs() {
        if (this._glyphs == null) {
            this._glyphs = this.font.convertTextToGlyphs(this._text);
        }
        return this._glyphs;
    }
    get font() {
        return this._font;
    }
    set font(value) {
        if (this._font !== value) {
            this._font.dispose();
            this._font = value;
            this.invalidateText();
        }
    }
    get fontSize() {
        return this._font.size;
    }
    set fontSize(value) {
        if (this._font.size !== value) {
            this._font.size = value;
            this.invalidateText();
        }
    }
    get fontStyle() {
        return this._font.style;
    }
    set fontStyle(value) {
        const f = this._font;
        if (f.style !== value) {
            this.font = FontManager.getFont(f.family, value, f.weight, f.size);
        }
    }
    get fontWeight() {
        return this._font.weight;
    }
    set fontWeight(value) {
        const f = this._font;
        if (f.weight !== value) {
            this.font = FontManager.getFont(f.family, f.style, value, f.size);
        }
    }
    /**
     * @override
     */
    drawOnPicture(context) {
        const glyphs = this.glyphs;
        if (glyphs.length === 0) {
            return null;
        }
        const font = this.font;
        let rect = null;
        switch (this._paintOrder) {
            case PaintOrder.FillStrokeMarkers:
            default:
                context.drawText(glyphs, this.fill, font);
                context.drawText(glyphs, this.stroke, font);
                rect = this.paintMarkers(context);
                break;
            case PaintOrder.FillMarkersStroke:
                context.drawText(glyphs, this.fill, font);
                rect = this.paintMarkers(context);
                context.drawText(glyphs, this.stroke, font);
                break;
            case PaintOrder.StrokeFillMarkers:
                context.drawText(glyphs, this.stroke, font);
                context.drawText(glyphs, this.fill, font);
                rect = this.paintMarkers(context);
                break;
            case PaintOrder.StrokeMarkersFill:
                context.drawText(glyphs, this.stroke, font);
                rect = this.paintMarkers(context);
                context.drawText(glyphs, this.fill, font);
                break;
            case PaintOrder.MarkersFillStroke:
                rect = this.paintMarkers(context);
                context.drawText(glyphs, this.fill, font);
                context.drawText(glyphs, this.stroke, font);
                break;
            case PaintOrder.MarkersStrokeFill:
                rect = this.paintMarkers(context);
                context.drawText(glyphs, this.stroke, font);
                context.drawText(glyphs, this.fill, font);
                break;
        }
        if (!rect) {
            // use the default picture bounds
            return null;
        }
        // enlarge to show markers
        return Rectangle.merge([this.pictureBounds, rect]);
    }
    dispose() {
        super.dispose();
        this._font.dispose();
        if (this._path) {
            this._path.delete();
            this._path = null;
        }
    }
    getElementAt(point) {
        if (this.hidden || this.locked) {
            return null;
        }
        return this.globalTightBounds.contains(point.x, point.y) ? this : null;
    }
    get pathToDraw() {
        if (this._path === null) {
            this._path = this._font.nativeFont.textToPath(this._text);
        }
        return this._path;
    }
    cloneCurrent(newId) {
        return new TextElement(this.document, this._text, this._font.clone(), newId ? null : this._id);
    }
    invalidateText() {
        this.invalidateBoundsAndPicture();
        this._glyphs = null;
        if (this._path) {
            this._path.delete();
            this._path = null;
        }
    }
}
